* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  font-size: 20px;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.flag-name {
  width: 50px;
  margin-right: 20px;
}
.country-item {
  font-size: 24px;
  display: flex;
  align-items: center;
}
li {
  list-style: none;
}
ul {
  padding: 0;
}

.wrap {
  font-weight: 700;
}

.country-info-item {
  font-size: 25px;
}
